const debounce = (fn, delay) => {
  var timeoutID = null
  return function () {
    clearTimeout(timeoutID)
    var args = arguments // Argumentos pasados a la función
    var that = this

    timeoutID = setTimeout(function () {
      fn.apply(that, args) // Invoca la función pasándole los argumentos
    }, delay)
  }
}

const capitalizeString = str => {
  return str
    .toLowerCase()
    .replace(/(?:^|\s|["'([{])+\S/g, match => match.toUpperCase())
}

const cortaString = (str, maxlength) => {
  return str.length > maxlength ? str.substr(0, maxlength) : str
}

const sortByAlbhabet = (arr = Array, propToCompare = String) => { // Recibe un arreglo de objetos y los ordena alfabeticamente de acuerdo a una propiedad
  return arr.sort((a, b) => a[propToCompare].normalize().localeCompare(b[propToCompare].normalize()))
}

const formatNumberWithSeparator = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

module.exports = {
  debounce,
  capitalizeString,
  cortaString,
  sortByAlbhabet,
  formatNumberWithSeparator,
}
