<template>
  <div>
    <v-container class="charlas__container">
      <title-ribbon :title="'Charlas y Talleres'" />
      <div
        v-if="charged"
        class="charlas_container__main"
      >
        <v-row class="charlas_row__img">
          <v-col>
            <v-img
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto,c_scale/v1637003360/terapiacl/charlas-y-talleres/talleres.png"
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charlas_row__text">
          <v-col>
            <p class="p_first">
              Las Charlas y Talleres que realizamos son actividades grupales
              aplicables a la vida cotidiana para personas con intereses
              comunes. Las temáticas de estas actividades tienen que ver con el
              mejoramiento, promoción y prevención de la salud mental,
              emocional, conductual, familiar y el desarrollo de estilos de vida
              saludables de los participantes o de terceros.
            </p>
            <p class="p_middle">
              La <strong>Charla </strong>
              Consiste en la exposición de un contenido específico y cuyo
              objetivo es el logro de nuevos conocmimientos acerca de dicho
              contenido.
            </p>
            <p class="p_last">
              El <strong>Taller </strong>
              Es una metodología de trabajo en grupo de "aprender haciendo".
              Dicha metodología permite a los participantes el aprendizaje y
              desarroiolo de destrezas para resolver problemas emocionales,
              conductuales y sociales.
            </p>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charlas_row__filtros">
          <v-col>
            <v-row>
              <v-col class="col-12 pt-0 input-col-title">
                <h4>Filtra tu Búsqueda:</h4>
              </v-col>
              <v-col class="col-12 col-md-4 input-col-left">
                <v-select
                  v-model="inputRangoEtario"
                  class="crf-select"
                  :items="rangos_etarios"
                  :menu-props="{ maxHeight: '400' }"
                  label="Rango Etario"
                  item-text="NombreRangoEdad"
                  item-value="CodRangoEdad"
                  persistent-hint
                  filled
                  dense
                />
              </v-col>
              <v-col class="col-12 col-md-4 input-col-middle">
                <v-select
                  v-model="inputTemas"
                  class="crf-select"
                  :items="temas"
                  :menu-props="{ maxHeight: '400' }"
                  label="Temas a Tratar"
                  item-text="Descripcion"
                  item-value="CodTema"
                  multiple
                  persistent-hint
                  filled
                  dense
                />
              </v-col>
              <v-col class="col-12 col-md-4 input-col-right">
                <v-text-field
                  v-model="inputTitulo"
                  class="crf-text-field"
                  label="Buscar"
                  filled
                  dense
                  success
                />
              </v-col>
            </v-row>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charlas_row__boxes">
          <v-col
            v-for="(charla, i) in charlas"
            :key="i"
            class="col-12 cbr-col-item"
          >
            <v-row>
              <v-col class="col-12 col-md-10 item__miniature">
                <div
                  class="im-main"
                  @click="$router.push(`/charlas/charla/${charla.ToLink}`)"
                >
                  <div class="title">
                    <h3>
                      {{ charla.Titulo }}
                    </h3>
                  </div>
                  <div class="content">
                    <v-img
                      class="imagen"
                      :src="
                        `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto:low/v1637004121/terapiacl/charlas-y-talleres/charlas-imagenes-miniaturas/${charla.ImagenMiniatura}.png`
                      "
                      lazy-src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto,c_scale/terapiacl/v1636641224/imagenes-terapeutas/fotos/1/default.png"
                    />
                    <div class="imc_text">
                      <span class="introduccion">
                        {{
                          charla.Introduccion.length > 300
                            ? charla.Introduccion.substr(0, 300)
                            : charla.Introduccion
                        }}
                        <strong>(Leer Más)</strong>
                      </span>
                      <div class="bottom">
                        <div class="separator" />
                        <span class="lugar">
                          <strong>LUGAR: </strong>{{ charla.Lugar }}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </v-col>
              <v-col class="col-12 col-md-2 item__date">
                <div class="title">
                  <h3>FECHA</h3>
                </div>
                <div class="content">
                  <div
                    v-if="charla.MesPalabra == null"
                    class="undefined"
                  >
                    <h2>POR DEFINIR</h2>
                  </div>
                  <div
                    v-else
                    class="defined"
                  >
                    <span class="mes">{{ charla.MesPalabra }}</span>
                    <span class="dia">{{ charla.Dia }}</span>
                    <div class="separator" />
                    <span
                      class="hora"
                    >{{ `${charla.Tiempo.hora}: ${charla.Tiempo.minuto}` }}
                    </span>
                    <span class="hora_word">Horas</span>
                  </div>
                </div>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
        <v-row class="charlas_row__pagination">
          <v-col class="trb_main">
            <v-pagination
              v-if="totalCharlas > limite"
              v-model="pagina"
              :length="Number(totalPaginas)"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              previous-aria-label="ps"
              next-aria-label="sd"
              color="success"
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="charlas_row__bottom">
          <v-col class="crb_main">
            <p>
              Si necesitas realizar algunas de nuestras actividades para una
              empresa, colegio u otro tipo de organización, puedes solicitarla
              <router-link to="/convenios-empresas">
                aquí.
              </router-link>
            </p>
          </v-col>
        </v-row>
      </div>
      <div v-else>
        <v-row>
          <v-col class="text-center">
            <v-progress-circular
              :size="70"
              color="success"
              indeterminate
              width="5"
            />
          </v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>
<script>
// import { mapState } from 'vuex'
  import Vue from 'vue'
  import { debounce } from '@/helpers'
  export default {
    name: 'Charlas',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
    },
    data () {
      return {
        charged: false,
        // sucursal: false,
        sucursalToLink: false,
        buttonsCharged: false,
        inputTitulo: null,
        inputRangoEtario: null,
        inputTemas: null,
        temas: [],
        rangos_etarios: [],
        charlas: '',
        // paginacion
        pagina: 1,
        limite: 100,
        totalPaginas: '',
        totalCharlas: '',
      }
    },
    watch: {
      inputRangoEtario: debounce(function () {
        this.getCharlas()
      }, 1500),
      inputTemas: debounce(function () {
        this.getCharlas()
      }, 1500),
      inputTitulo: debounce(function () {
        this.getCharlas(this.inputTitulo)
      }, 1500),
      pagina: function () {
        this.getCharlas()
      },
    },
    created () {
      this.getAll()
      this.getCharlas()
    },
    methods: {
      getCharlas (bySearch = '') {
        if (bySearch) {
          this.pagina = 1
        }
        const bytitle = bySearch.split(' ').filter(a => a)
        const desde = (this.pagina - 1) * this.limite
        const body = {
          rango_etario: this.inputRangoEtario,
          temas: this.inputTemas,
          bytitle,
          desde,
          limite: this.limite,
        }
        Vue.axios
          .post('/api/charlas', body)
          .then(response => {
            this.charlas = response.data.result
            this.totalCharlas = response.data.total
            this.totalPaginas = Math.round(this.totalCharlas / this.limite)
          })
          .catch(err => {
            console.log(err)
          })
      },
      getRangosEtarios () {
        const body = {
          codSucursal: 1,
        }
        Vue.axios
          .post('/api/rangos-etarios/siat', body)
          .then(response => {
            this.rangos_etarios = response.data.result
          })
          .catch(err => {
            console.log(err)
          })
      },
      getCharlasTemas () {
        Vue.axios
          .post('/api/charlas/temas')
          .then(response => {
            this.temas = response.data.temas
          })
          .catch(err => {
            console.log(err)
          })
      },
      getAll () {
        this.getCharlasTemas()
        this.getRangosEtarios()
        this.charged = true
      },
    },
    metaInfo: {
      title: 'charlas',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
